@import "./variables";

.page {
  display: grid;
  grid-template: auto / 1fr 400px;
  column-gap: 20px;
  min-height: 100vh;
  background: radial-gradient(
    circle at 10% 20%, rgb(0, 0, 0) 0%, rgb(64, 64, 64) 90.2%
  );
}

.page-content {
  padding: $padding-main;
}

.sidebar {
  padding: $padding-main;
}
